<template>
  <div id="container">
    <div class="radio-group">
      <b-form-radio-group
        v-model="selectedCollege"
        :options="collegeOptions"
        value-field="no"
        text-field="name"
        inline
      ></b-form-radio-group>
    </div>
    <div class="slider">
      <b-form-checkbox
        v-model="enableTimeAxis"
      >使用時間軸</b-form-checkbox>
      <vue-slider ref="slider" v-model="displayDate"
        :data="timeAxisData" :lazy="true" :hide-label="true" :marks="false" :process="true"
        :data-value="'value'" :data-label="'label'"/>
    </div>
    <div id="map"></div>
    <div id="popup" class="ol-popup">
      <a href="#" id="popup-closer" class="ol-popup-closer"></a>
      <div id="popup-existing" class="hide">
        <div class="name">{{markerName}}</div>
        <div class="transfered">{{markerTransfered}}</div>
        <div class="note">{{markerNote}}</div>
        <div class="appendix">{{markerAppendix}}</div>
        <!-- <div>{{markerLat}}</div>
        <div>{{markerLong}}</div> -->
        <!-- <button @click.stop="deletePoint">刪除標記</button> -->
      </div>
      <div id="popup-new" class="hide">
        <div><input placeholder="輸入顯示名稱" v-model="markerName" /></div>
        <!-- <div>緯度{{markerLat}}</div>
        <div>經度{{markerLong}}</div> -->
        <!-- <button @click.stop="getFeatureProp">新增標記</button> -->
      </div>
    </div>
  </div>
</template>

<script>
import 'ol/ol.css';
import Feature from 'ol/Feature';
import Map from 'ol/Map';
import Point from 'ol/geom/Point';
import View from 'ol/View';
import Overlay from 'ol/Overlay';
import {Circle as CircleStyle, Stroke, Style, Fill, Icon} from 'ol/style';
import {OSM, Vector as VectorSource} from 'ol/source';
import {Tile as TileLayer, Vector as VectorLayer} from 'ol/layer';
import {easeOut} from 'ol/easing';
import {fromLonLat} from 'ol/proj';
import {getVectorContext} from 'ol/render';
import {unByKey} from 'ol/Observable';
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/antd.css'
import { localForageService } from '../store/localForageService';
import rawData from '../store/rawData';
export default {
  name: 'Map',
  props: {
    msg: String
  },
  components: {
    VueSlider
  },
  data: function () {
    return {
      map: null,
      tileLayer: null,
      source: null,
      vector: null,
      overlay: null,
      duration: 3000,
      // markerLong: '',
      // markerLat: '',
      markerName: '',
      markerNote: '',
      markerAppendix: '',
      markerTransfered: '',
      focusingFeature: null,
      storedFeatureProps: [],
      autoplaying: false,
      autoplayIndex: 0,
      selectedCollege: 'all',
      displayDate: new Date('1930-01'),
      collegeOptions: [],
      enableTimeAxis: false,
      startMonth: new Date('1931-01-01'),
      endMonth: new Date('1930-01-31'),
      timeAxisData: [{label: '1930-01', value: new Date('1930-01')}],
    }
  },
  watch: {
    selectedCollege: function () {
      this.redrawFeatures();
    },
    storedFeatureProps: function() {
      this.updateCollegeOptions();
      this.updateTimeAxis();
      this.redrawFeatures();
    },
    displayDate: function() {
      if (!this.enableTimeAxis) return;
      this.redrawFeatures();
    },
    enableTimeAxis: function() {
      this.redrawFeatures();
    }
  },
  computed: {
    popupContainer: function () {
      return document.getElementById('popup');
    },
    popupExisting: function () {
      return document.getElementById('popup-existing');
    },
    popupNew: function () {
      return document.getElementById('popup-new');
    },
    popupCloser: function () {
      return document.getElementById('popup-closer');
    },
  },
  methods: {
    addRandomFeature: function () {
      var x = Math.random() * 360 - 180;
      var y = Math.random() * 180 - 90;
      var geom = new Point(fromLonLat([x, y], process.env.MAP_PROJ));
      var feature = new Feature(geom);
      this.source.addFeature(feature);
      return feature;
    },
    createNewFeature: function ({uuid, no, name, time, branch, transfered, note,
      appendix, lat, long}) {
      var geom = new Point(fromLonLat([long, lat], process.env.MAP_PROJ));
      var feature = new Feature(geom);
      feature.set('uuid', uuid);
      feature.set('no', no);
      feature.set('name', name);
      feature.set('time', time);
      feature.set('branch', branch);
      feature.set('transfered', transfered);
      feature.set('note', note);
      feature.set('appendix', appendix);
      feature.setStyle(new Style({
        image: new Icon({ src: `img/${no}.png`, scale: 0.05 }),
        fill: new Fill({ color: 'purple', }),
        stroke: new Stroke({ color: 'purple', width: 1.25}),
      }));
      this.source.addFeature(feature);
    },
    flash: function (feature) {
      var start = new Date().getTime();
      var listenerKey = this.tileLayer.on('postrender', (event) => {
        var vectorContext = getVectorContext(event);
        var frameState = event.frameState;
        var flashGeom = feature.getGeometry().clone();
        var elapsed = frameState.time - start;
        var elapsedRatio = elapsed / this.duration;
        // radius will be 5 at start and 30 at end.
        var radius = easeOut(elapsedRatio) * 25 + 5;
        var opacity = easeOut(1 - elapsedRatio);

        var style = new Style({
          image: new CircleStyle({
            radius: radius,
            stroke: new Stroke({
              color: 'rgba(255, 0, 0, ' + opacity + ')',
              width: 0.25 + opacity,
            }),
          }),
        });
        vectorContext.setStyle(style);
        vectorContext.drawGeometry(flashGeom);
        if (elapsed > this.duration) {
          unByKey(listenerKey);
          return;
        }
        // tell OpenLayers to continue postrender animation
        this.map.render();
      });
    },
    initMap: function () {
      this.tileLayer = new TileLayer({
        source: new OSM({
          wrapX: false,
        }),
      });
      this.overlay = new Overlay({
        element: this.popupContainer,
        autoPan: true,
        autoPanAnimation: {
          duration: 250
        }
      });
      this.map = new Map({
        target: 'map',
        layers: [this.tileLayer],
        overlays: [this.overlay],
        view: new View({
          projection: process.env.MAP_PROJ,
          center: [0, 0],
          zoom: 3,
          multiWorld: true,
        })
      });
      this.source = new VectorSource({
        wrapX: false,
      });
      this.vector = new VectorLayer({
        source: this.source,
        style: new Style({
          image: new CircleStyle({ // replace this with image
            radius: 5,
            fill: new Fill({
              color: 'purple',
            }),
            stroke: new Stroke({
              color: 'purple',
              width: 5,
            }),
          }),
        })
      });
      this.map.addLayer(this.vector);
      this.map.addOverlay(this.overlay);

      this.popupCloser.onclick = this.hidePopup;

      this.map.on('singleclick', (event) => {
        if (this.map.hasFeatureAtPixel(event.pixel) === true) {
          const coordinate = event.coordinate;
          // this.markerLat = coordinate[1];
          // this.markerLong = coordinate[0];
          this.overlay.setPosition(coordinate);
          // always use 1st of features list...
          this.focusingFeature = this.map.getFeaturesAtPixel(event.pixel)[0];
          if (!this.focusingFeature.get('uuid')) return;
          this.markerName = `${this.focusingFeature.get('name') || ''} \
            ${this.focusingFeature.get('branch') || ''}`;
          this.markerTransfered = this.focusingFeature.get('transfered') || '';
          this.markerNote = this.focusingFeature.get('note') || '';
          this.markerAppendix = this.focusingFeature.get('appendix') || '';
          this.popupNew.classList.add('hide');
          this.popupExisting.classList.remove('hide');
        } else {
          this.hidePopup();
        }
      });
    },
    clearAll: function () {
      localForageService.setItem('saveFeatureProp', []).then(() => {
        this.storedFeatureProps = [];
        this.source.clear();
      });
    },
    overview: function () {
      if (this.autoplaying) {
        console.log('cancel overview');
        this.map.getView().cancelAnimations();
        this.autoplaying = false;
        return;
      }
      this.autoplaying = true;
      this.autoplayIndex = 0;
      var centerToNextFeature = () => {
        if (!this.autoplaying) return;
        const cFeature = this.storedFeatureProps[this.autoplayIndex++];
        this.map.getView().animate({center: [cFeature.long, cFeature.lat]});
        if (this.autoplayIndex >= this.storedFeatureProps.length) {
          this.autoplaying = false;
          return
        }
        window.setTimeout(centerToNextFeature, 3000);
      }
      centerToNextFeature();
    },
    getFeatureProp: function ({編號, 校名, 分校, 時間, 經度, 緯度, 遷移地名, 備註, 附註}) {
      // console.log('getFeatureProp', 校名, 分校, 時間, 經度, 緯度, 遷移地名, 備註, 附註);
      const featureProp = {
        uuid: Date.now(),
        no: parseInt(編號),
        name: 校名,
        time: new Date(時間),
        branch: 分校,
        transfered: 遷移地名,
        note: 附註,
        appendix: 備註,
        lat: parseFloat(緯度),
        long: parseFloat(經度),
      };
      this.extendTimeAxis(時間);
      return featureProp;
    },
    // deletePoint: function () {
    //   const deletingUuid = this.focusingFeature.get('uuid');
    //   const deletingIndex = this.storedFeatureProps.findIndex((feature) => {
    //     return feature.uuid === deletingUuid;
    //   });
    //   if (deletingIndex === -1) return;
    //   this.storedFeatureProps.splice(deletingIndex, 1);
    //   this.source.removeFeature(this.focusingFeature);
    //   localForageService.setItem('saveFeatureProp', this.storedFeatureProps).then(() => {
    //     this.focusingFeature = null;
    //     this.hidePopup();
    //   });
    // },
    // saveFeature: function (featureProp) {
    //   this.storedFeatureProps.push(featureProp);
    //   localForageService.setItem('saveFeatureProp', this.storedFeatureProps);
    // },
    hidePopup: function () {
      this.popupNew.classList.add('hide');
      this.popupExisting.classList.add('hide');
      this.overlay.setPosition(undefined);
      this.popupCloser.blur();
      return false;
    },
    redrawFeatures: function () {
      this.hidePopup();
      this.source.clear();
      Object.entries(this.storedFeatureProps).filter(([no]) => {
        return this.selectedCollege === 'all' ||
          parseInt(no) === parseInt(this.selectedCollege);
      }).map(([no, list]) => {
        console.log('2nd map', no, list);
        return Object.entries(this.groupBy(list, 'branch'))
          .map(([branch, list]) => {
            console.log('branch', branch);
            console.log('list', list);
            console.log('this.enableTimeAxis', this.enableTimeAxis, typeof this.enableTimeAxis);
            console.log('this.displayDate', this.displayDate);
            if (this.enableTimeAxis) {
              const visibleLastDate = list.filter((fProp) => {
                return fProp.time <= this.displayDate;
              }).sort((a, b) => {
                return b.time - a.time
              })[0]?.time || null;
              console.log('visibleLastDate', visibleLastDate);
              return list.filter(({time}) => {
                console.log('fProp.time', time);
                return visibleLastDate &&
                  time.getFullYear() === visibleLastDate.getFullYear() &&
                  time.getMonth() === visibleLastDate.getMonth();
              });
            } else {
              return list;
            }
          });
      }).flat(2).forEach((finalProp) => {
        if (finalProp) {this.createNewFeature(finalProp);}
      });
    },
    extendTimeAxis: function (date) {
      const cDateObj = new Date(date);
      if (cDateObj < this.startMonth) {
        this.startMonth.setFullYear(cDateObj.getFullYear());
        // first date of cDateObj month
        this.startMonth.setMonth(cDateObj.getMonth(), 1);
      }
      if (this.endMonth < cDateObj) {
        this.endMonth.setFullYear(cDateObj.getFullYear());
        // last date of cDateObj month
        this.endMonth.setMonth(cDateObj.getMonth() + 1, 0);
      }
    },
    groupBy: function(xs, key) {
      return xs.reduce(function(rv, x) {
        (rv[x[key]] = rv[x[key]] || []).push(x);
        return rv;
      }, {});
    },
    updateCollegeOptions: function() {
      this.collegeOptions = [
        {no: 'all', name: 'All'},
        ...Object.entries(this.storedFeatureProps).map(
          ([key, list]) => {return {no: key, name: list[0].name}})
      ];
    },
    updateTimeAxis: function() {
      const newTimeAxis = [];
      const cAxisDateObj = new Date(this.startMonth);
      while(cAxisDateObj <= this.endMonth) {
        newTimeAxis.push({
          label: `${cAxisDateObj.getFullYear()}/${cAxisDateObj.getMonth()+1}`,
          value: new Date(cAxisDateObj)
        });
        cAxisDateObj.setMonth(cAxisDateObj.getMonth() + 1);
      }
      console.log('newTimeAxis', newTimeAxis);
      this.timeAxisData = newTimeAxis;
      this.$refs.slider.setIndex(0);
    },
  },
  mounted: async function () {
    this.initMap();
    const rawDataInit = await localForageService.getItem('rawDataInit');
    if (!rawDataInit) {
      // import rawData
      this.storedFeatureProps = this.groupBy(
        Array.from(rawData).map(this.getFeatureProp), 'no'
      );
      await localForageService.setItem('saveFeatureProp', this.storedFeatureProps);
      // put this line back!!!!!!
      // await localForageService.setItem('rawDataInit', true);
    }

  }
}
</script>

<style scoped>
  #container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
  .radio-group {
    /* height: 50px; */
    padding: 0 2rem;
  }
  .slider {
    padding: 2rem;
    /* height: 50px; */
  }
  #map {
    /* height: calc(100vh - 200px); */
    flex: 1;
  }
  .ol-popup {
    position: absolute;
    background-color: white;
    box-shadow: 0 1px 4px rgba(0,0,0,0.2);
    padding: 15px;
    border-radius: 10px;
    border: 1px solid #cccccc;
    bottom: 12px;
    left: -50px;
    min-width: 280px;
  }
  .ol-popup:after, .ol-popup:before {
    top: 100%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
  }
  .ol-popup:after {
    border-top-color: white;
    border-width: 10px;
    left: 48px;
    margin-left: -10px;
  }
  .ol-popup:before {
    border-top-color: #cccccc;
    border-width: 11px;
    left: 48px;
    margin-left: -11px;
  }
  .ol-popup-closer {
    text-decoration: none;
    position: absolute;
    top: 2px;
    right: 8px;
  }
  .ol-popup-closer:after {
    content: "✖";
  }
  .hide {
    display: none;
  }
  .note {
    min-width: 40vw;
    font-size: 0.8rem;
  }
  .appendix {
    font-size: 0.8rem;
    font-style: italic;
  }
</style>
